import React, {createContext} from "react";
import { TranslatorPack, Translator } from './translator';

const translatorContext = createContext<TranslatorPack>(Translator.ZhTW);

export const TranslatorContext = translatorContext;

const translatorContextProvider = ({pack, children} : {pack: TranslatorPack, children: JSX.Element}) => {
  const [tPack, setTPack] = React.useState(pack)

  return (
    <TranslatorContext.Provider value={tPack}>
        {children}
    </TranslatorContext.Provider>
  )
}

export const TranslatorContextProvider = translatorContextProvider;

export const useTranslatorContext = () => React.useContext(TranslatorContext);