import React, { useState } from 'react';
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import CameraIcon from '@material-ui/icons/PhotoCamera';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import CssBaseline from '@material-ui/core/CssBaseline';
import CircularProgress  from '@material-ui/core/CircularProgress';
import Grid from '@material-ui/core/Grid';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { makeStyles, Theme, } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import MuiLink from '@material-ui/core/Link';
import ProTip from '~/src/ProTip';
import Link from '~/src/link';
import ReactPlayer from 'react-player';

import Header from '~/src/header';
import Footer from'~/src/footer';
import { useTranslatorContext } from '~/src/utility/translator/translatorContext';

const useStyles = makeStyles((theme: Theme) => ({
  icon: {
    marginRight: theme.spacing(2),
  },
  heroContent: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(8, 0, 6),
  },
  heroButtons: {
    marginTop: theme.spacing(4),
  },
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8),
  },
  card: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
  },
  cardMedia: {
    paddingTop: '56.25%', // 16:9
  },
  cardContent: {
    flexGrow: 1,
  },
  footer: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(6),
  },
  videoWrapper: {
    position: "relative",
    paddingBottom: "56.25%",
    paddingTop: 25,
    backgroundColor: "#000000aa",
    "& iframe": {
      position: "absolute",
      top: 0,
      left: 0,
      width: "100%",
      height: "100%",
    }
  }
}));

export default function Index() {
  const classes = useStyles({});

  return (
    <React.Fragment>
      { /* <div style={{display: 'flex', flexDirection: 'column',  minHeight: '100vh',}}> */ /* 頁面太短用*/ }
      <CssBaseline />
      <Header tabIndex={0}/>
      <main>
      <CardGenerator classes={classes}/>
      </main>
      {/* Footer */}
      <Footer />
      {/* End footer */}
    </React.Fragment>
  );
}

function CardGenerator({classes} : {classes?: any}) {
  const t = useTranslatorContext();

  const softwareDatas = 
    [ 
      {title: t.tutorial.modern_intro, subtitle: t.tutorial.modern_intro_desc, url: "https://www.youtube.com/embed/zcT7KYsxPJI",　backgroundColor: "lightgoldenrodyellow", color: "#000000"},,
      {title: t.tutorial.modern_oneclick, subtitle: t.tutorial.modern_oneclick_desc, url: "https://www.youtube.com/embed/SnSE_lr5Gpw",　backgroundColor: "plum", color: "#000000"},,
      {title: t.tutorial.modern_installer, subtitle: t.tutorial.modern_installer_desc, url: "https://www.youtube.com/embed/E9auOMjV3qg",　backgroundColor: "lightgreen", color: "#000000"},
    ];
  const traditionalDatas = 
    [ 
      {title: t.tutorial.legacy_zero, subtitle: t.tutorial.legacy_zero_desc, url: "https://www.youtube.com/embed/7Pp2UeKL5I4",　backgroundColor: "lightskyblue", color: "#000000"},,
      {title: t.tutorial.legacy_forge, subtitle: t.tutorial.legacy_forge_desc, url: "https://www.youtube.com/embed/9TE5gg8QqK0",　backgroundColor: "aliceblue", color: "#000000"},,
    ];
  return (
    <Container className={classes.cardGrid} maxWidth="md">
      <Typography variant="h4" gutterBottom style={{paddingBottom: 16}}>
        {/* 現代化開服教學 (創世神權杖) */}
        {t.tutorial.modern_title}
      </Typography>
      <Grid container spacing={4}>
        {softwareDatas.map(card => (
          <Grid item key={card!.title} xs={12} sm={12} md={12}>
            <Card className={classes.card}>
              <div className={classes.videoWrapper}>
                {/* <iframe src={card.url} frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe> */}
                <IframeLoader url={card!.url}/>
              </div>
              <CardContent className={classes.cardContent} style={{backgroundColor: card!.backgroundColor, color: card!.color}}>
                <Typography gutterBottom variant="h5" component="h2">
                  {`${card!.title}`}
                </Typography>
                <Typography>
                  {`${card!.subtitle}`}
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
      <Box mt={16}/>
      <Typography variant="h4" gutterBottom style={{paddingBottom: 16}}>
        {/* 傳統開服教學 */}
        {t.tutorial.legacy_title}
      </Typography>
      <Grid container spacing={4}>
        {traditionalDatas.map(card => (
          <Grid item key={card!.title} xs={12} sm={12} md={12}>
            <Card className={classes.card}>
              <div className={classes.videoWrapper}>
                {/* <iframe src={card.url} frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe> */}
                <IframeLoader url={card!.url}/>
              </div>
              <CardContent className={classes.cardContent} style={{backgroundColor: card!.backgroundColor, color: card!.color}}>
                <Typography gutterBottom variant="h5" component="h2">
                  {`${card!.title}`}
                </Typography>
                <Typography>
                  {`${card!.subtitle}`}
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Container>
  );
}

const iframStyle = makeStyles((_) => ({
  circular: {
    position: "absolute !important" as any,
    left: "calc(50% - 60px) !important" as any,
    top: "calc(50% - 60px) !important" as any,
    color: "antiquewhite" as any,
  },
}));

function IframeLoader({url} : {url: string}) {
  const classes = iframStyle({});
  
  const [loading, setLoading] = useState(false);

  return <React.Fragment>
    <iframe src={url} frameBorder="0" onLoad={_ => setLoading(true)} allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
    {
      !loading &&
      <CircularProgress classes={{
        root: classes.circular,
      }} size={120} />
    }
  </React.Fragment>
}

