import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import CameraIcon from '@material-ui/icons/PhotoCamera';
import MailIcon from '@material-ui/icons/Mail';
import NotificationsIcon from '@material-ui/icons/Notifications';
import MoreIcon from '@material-ui/icons/MoreVert';
import AccountCircle from '@material-ui/icons/AccountCircle';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Link from './link';

import VideocamIcon from '@material-ui/icons/Videocam';
import StarsIcon from '@material-ui/icons/Stars';
import QuestionAnswerIcon from '@material-ui/icons/QuestionAnswer';
import ChangeHistoryIcon from '@material-ui/icons/ChangeHistory';
import SponsorIcon from '@material-ui/icons/LocalAtm';
import DeckIcon from '@material-ui/icons/Deck';
import { useTranslatorContext } from './utility/translator/translatorContext';

const useStyles = makeStyles(theme => ({
    grow: {
      flexGrow: 1,
    },
    icon: {
      marginRight: theme.spacing(2),
    },
    link: {
      color: "#ffffff",
      margin: theme.spacing(1, 1.5), // https://material-ui.com/customization/spacing/
    },
    sectionDesktop: {
      display: 'none',
      [theme.breakpoints.up('md')]: {
        display: 'flex',
      },
    },
    sectionMobile: {
      display: 'flex',
      [theme.breakpoints.up('md')]: {
        display: 'none',
      },
    },
    padding: {
      padding: theme.spacing(3),
    },
}));

interface HeaderProps {
  tabIndex: number;
}

export default function Header(props: HeaderProps) {
  const classes = useStyles({});
  const t = useTranslatorContext();
  console.log("rebuild header");
  console.log(classes);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState(null);
  const [value, setValue] = React.useState(props.tabIndex < 0 ? false : props.tabIndex);

  const isMenuOpen = Boolean(anchorEl);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

  // const handleProfileMenuOpen = (event) => {
  //   setAnchorEl(event.currentTarget);
  // };

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
    handleMobileMenuClose();
  };

  // const handleMobileMenuOpen = (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
  //   setMobileMoreAnchorEl(event.currentTarget);
  // };

  const handleMobileMenuOpen = (event: any) => {
    console.log("open menu");
    setMobileMoreAnchorEl(event.currentTarget);
    console.log("open menu");
  };

  const handleChange = (_: React.ChangeEvent<{}>, newValue: number) => {
    setValue(newValue);
  };


  const menuId = 'primary-search-account-menu';
  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={menuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <MenuItem onClick={handleMenuClose}>Profile</MenuItem>
      <MenuItem onClick={handleMenuClose}>My account</MenuItem>
    </Menu>
  );

  const mobileMenuId = 'primary-search-account-menu-mobile';
  const renderMobileMenu = (
    <Menu
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
    >
      <MenuItem component={Link} href='/tutorials'>
        <IconButton
          aria-label="account of current user"
          aria-controls="primary-search-account-menu"
          aria-haspopup="true"
          color="inherit"
        >
          <VideocamIcon/>
        </IconButton>
        <p>{t.header.tutorial}</p>
      </MenuItem>
      <MenuItem component={Link} href='/installers'>
        <IconButton
          aria-label="account of current user"
          aria-controls="primary-search-account-menu"
          aria-haspopup="true"
          color="inherit"
        >
          <StarsIcon/>
        </IconButton>
        <p>{t.header.suggested}</p>
      </MenuItem>
      <MenuItem component={Link} href='/faq'>
        <IconButton
          aria-label="account of current user"
          aria-controls="primary-search-account-menu"
          aria-haspopup="true"
          color="inherit"
        >
          <QuestionAnswerIcon/>
        </IconButton>
        <p>{t.header.faq}</p>
      </MenuItem>
      <MenuItem component={Link} href='/changelogs'>
        <IconButton
          aria-label="account of current user"
          aria-controls="primary-search-account-menu"
          aria-haspopup="true"
          color="inherit"
        >
          <ChangeHistoryIcon/>
        </IconButton>
        <p>{t.header.changelog}</p>
      </MenuItem>
      <MenuItem component={Link} href='/sponsors'>
        <IconButton
          aria-label="account of current user"
          aria-controls="primary-search-account-menu"
          aria-haspopup="true"
          color="inherit"
        >
          <SponsorIcon/>
        </IconButton>
        <p>{t.header.sponsor}</p>
      </MenuItem>
      <MenuItem component={Link} href='/about'>
        <IconButton
          aria-label="account of current user"
          aria-controls="primary-search-account-menu"
          aria-haspopup="true"
          color="inherit"
        >
          <DeckIcon/>
        </IconButton>
        <p>{t.header.about}</p>
      </MenuItem>
    </Menu>
  );

  return (
      <AppBar position="relative">
          <Toolbar>
              <img src="/images/header/logo.png" style={{ marginRight: 16, width: 48, height: 48}}/>
              <Link 
                  aria-label="tutorial for minecraft scepter"
                  variant="h6"
                  href="/"
                  style={{color: "white"}}
                >
                  {t.header.title_logo}
              </Link>
              <div className={classes.grow} />
              <nav className={classes.sectionDesktop}>
                {
                // <Link 
                //   aria-label="tutorial for minecraft scepter"
                //   variant="button"
                //   color="textPrimary"
                //   href="#" 
                //   className={classes.link}
                // >
                //   影片教學
                // </Link>
                // <Link 
                //   aria-label="recommend installer package"
                //   variant="button"
                //   color="textPrimary"
                //   href="#" 
                //   className={classes.link}
                // >
                //   推薦安裝包
                // </Link>
                // <Link 
                //   aria-label="changelog of minecraft scepter"
                //   variant="button"
                //   color="textPrimary"
                //   href="#" 
                //   className={classes.link}
                // >
                //   歷史版本
                // </Link>
                // <Link 
                //   aria-label="about the author"
                //   variant="button"
                //   color="textPrimary"
                //   href="#" 
                //   className={classes.link}
                // >
                //   關於
                // </Link>
                }
                <StyledTabs value={value} onChange={handleChange} aria-label="styled tabs example">
                  <StyledTab label={t.header.tutorial} component={Link} href='/tutorials'/>
                  <StyledTab label={t.header.suggested} component={Link} href='/installers'/>
                  <StyledTab label={t.header.faq} component={Link} href='/faq'/>
                  <StyledTab label={t.header.changelog} component={Link} href='/changelogs'/>
                  <StyledTab label={t.header.sponsor} component={Link} href='/sponsors'/>
                  <StyledTab label={t.header.about} component={Link} href='/about'/>
                </StyledTabs>
                <Typography className={classes.padding} />
              </nav>
              <nav className={classes.sectionMobile}>
                <IconButton
                  aria-label="show more"
                  aria-controls={mobileMenuId}
                  aria-haspopup="true"
                  onClick={(event) => {
                    console.log("clicked!");
                    handleMobileMenuOpen(event);
                    console.log("clicked!");
                  }}
                  color="inherit"
                >
                  <MoreIcon />
                </IconButton>
              </nav>
          </Toolbar>
          {renderMobileMenu}
          {renderMenu}
      </AppBar>
  );
}

interface StyledTabsProps {
  value: number | boolean;
  onChange: (event: React.ChangeEvent<{}>, newValue: number) => void;
}

const StyledTabs = withStyles({
  indicator: {
    display: 'flex',
    justifyContent: 'center',
    backgroundColor: 'transparent',
    '& > div': {
      maxWidth: 40,
      width: '100%',
      backgroundColor: '#635ee7',
    },
  },
})((props: StyledTabsProps) => <Tabs {...props} TabIndicatorProps={{ children: <div /> }} />);

interface StyledTabProps {
  label: string;
  component: any;
  href: any;
}

const StyledTab = withStyles(theme => ({
  root: {
    minWidth: "auto",
    textTransform: 'none',
    color: '#fff',
    fontWeight: theme.typography.fontWeightRegular,
    fontSize: theme.typography.pxToRem(15),
    marginRight: theme.spacing(1),
    '&:focus': {
      opacity: 1,
    },
  },
}))((props: StyledTabProps) => <Tab disableRipple {...props} />);