import React from 'react';
import Typography from '@material-ui/core/Typography';
import MuiLink from '@material-ui/core/Link';
import { makeStyles } from '@material-ui/core/styles';
import { useTranslatorContext } from './utility/translator/translatorContext';


const useStyles = makeStyles(theme => ({
  footer: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(6),
    marginTop: 'auto',
  },
}));


function Copyright() {
  const t = useTranslatorContext();
    return (
      <Typography variant="body2" color="textSecondary" align="center">
        {/* {'Copyright © '} */}
        { t.footer.copy_right }
        <MuiLink color="inherit" href="https://material-ui.com/">
          {/* {創世神權杖} */}
          { t.footer.minecraft_scepter }
        </MuiLink>{' '}
        {new Date().getFullYear()}
        {'.'}
      </Typography>
    );
}

export default function Footer({style}: { style?: any } = {}) {
    const classes = useStyles({});
    const t = useTranslatorContext();
    return (
      <footer className={classes.footer} style={style}>
        <Typography variant="h6" align="center" gutterBottom>
          {t.footer.title}
        </Typography>
        <Typography variant="subtitle1" align="center" color="textSecondary" component="p">
          {t.footer.subtitle}
        </Typography>
        <Copyright />
      </footer>
    );
  }